.eventImage{
	-ms-flex: 0 0 auto;
	    flex: 0 0 auto;
	padding: 20px 0;
	height: 100%;
	width: 300px;
	margin-left: 30px;
	cursor: pointer;
	background-color: #e5e5e5;
}

.eventImage h5{
	padding-left: 20px;
}

.main.event{
	padding-left: 15%;
	position: relative;
	overflow: hidden;
}

.eventImage img{
	height: 50%;
	width: 100%;
	-o-object-fit: cover;
	   object-fit: cover;
}

.desc{
	padding: 20px;
	margin-left: 30px;
	width: 40%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-line-pack: start;
	    align-content: flex-start;
}

.desc .place{
	height: 5%;
	margin: 0;
	vertical-align: bottom;
	font-size: 1.2em;
}

.desc .date{
	height: 5%;
}

.desc .bio{
	width: 35%;
	font-family: Arial, Helvetica, sans-serif;
	padding: 20px 0 0 0;
	margin-top: 30px;
	overflow-y: auto;
}

@media (max-width: 1080px) {
	
	.piramida{
		display: none;
	}

}

@media (max-width: 960px) {
	.text{
		top: 5%;
		left: 10%;
		width: 60vmax;
		overflow: visible;
	}

	h1{
		font-size: 1em;
	}
	
}


