@-webkit-keyframes emerge {
  0% {
  	-webkit-transform: translateY(100px);
  	        transform: translateY(100px);
  }
  100% {
  	-webkit-transform: translateY(0);
  	        transform: translateY(0);
  }
}

@keyframes emerge {
  0% {
  	-webkit-transform: translateY(100px);
  	        transform: translateY(100px);
  }
  100% {
  	-webkit-transform: translateY(0);
  	        transform: translateY(0);
  }
}

@-webkit-keyframes leftemerge {
  0% {
  	-webkit-transform: translateX(-100px);
  	        transform: translateX(-100px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

@keyframes leftemerge {
  0% {
  	-webkit-transform: translateX(-100px);
  	        transform: translateX(-100px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

@-webkit-keyframes leftemerge2 {
  0% {
  	-webkit-transform: translateX(-200px);
  	        transform: translateX(-200px);
  }
  50%{
  	-webkit-transform: translateX(-200px);
  	        transform: translateX(-200px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

@keyframes leftemerge2 {
  0% {
  	-webkit-transform: translateX(-200px);
  	        transform: translateX(-200px);
  }
  50%{
  	-webkit-transform: translateX(-200px);
  	        transform: translateX(-200px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

@-webkit-keyframes rightemerge {
  0% {
  	-webkit-transform: translateX(100px);
  	        transform: translateX(100px);
  }
  50%{
  	-webkit-transform: translateX(100px);
  	        transform: translateX(100px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

@keyframes rightemerge {
  0% {
  	-webkit-transform: translateX(100px);
  	        transform: translateX(100px);
  }
  50%{
  	-webkit-transform: translateX(100px);
  	        transform: translateX(100px);
  }
  100% {
  	-webkit-transform: translateX(0);
  	        transform: translateX(0);
  }
}

.emerge{
	-webkit-animation: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86) emerge forwards;
	        animation: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86) emerge forwards;
}

.leftemerge{
	-webkit-animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge forwards;
	        animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge forwards;
}

.leftemerge2{
	-webkit-animation: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge2 forwards;
	        animation: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge2 forwards;
}

.leftemerge3{
	-webkit-animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge3 forwards;
	        animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge3 forwards;
}

.rightemerge{
	-webkit-animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) rightemerge forwards;
	        animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) rightemerge forwards;
}



.template{
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	height: 100%;
	width: 100%;
}

.back{
	height: 5%;
	position: relative;
	width: 100%;
	font-size: 1.1em;
	margin: 20px 0 5px 30px;
	text-align: left;
	padding-bottom: 10px;
}

span{
	left: 15%;
	position: relative;
	overflow: hidden;
	display: inline-block;
}

.title{
	height: 15%;
	width: 100%;
	font-size: 3em;
	margin: 5px 0 30px 30px;
	text-align: left;
}

.main{
	height: 70vw;
	max-height: 400px;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	background-color: #9fb7a7;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
}

.main::-webkit-scrollbar { 
    display: none; 
}

.item{
	-ms-flex: 0 0 auto;
	    flex: 0 0 auto;
	display: inline-block;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	top: 0;
	height: 100%;
	padding: 35px 0;
	width: auto;
	cursor: pointer;
	overflow: hidden;
}

.item:hover{
	background-color: #aba;
}

.description{
	display: inline-block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	height: 80px;
	position: relative;
	width: 100%;
	bottom: -100px;
	padding: 10px;
	background-color:rgba(255, 255, 255, 0.9);
	-webkit-transition: 100ms bottom ease-in;
	-o-transition: 100ms bottom ease-in;
	transition: 100ms bottom ease-in;
}

.photolink{
	display: inline-block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	height: 100%;
}

.item a img{
	display: block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: auto;
	height: 100%;
	padding: 0 30px;
	-o-object-fit: cover;
	   object-fit: cover;
	border-left: 1px solid #839f8e;
	-webkit-filter: saturate(0.7);
	        filter: saturate(0.7);
}
.name{
	height: 15%;
	width: 100%;
	font-size: 1em;
	margin: 10px;
	text-align: left;
}

.placedate{
	left: 15%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.place{
	height: 10%;
	width: 100%;
	font-size: 0.8em;
	margin: 0 0 10px 10px;
	text-align: left;
}

.date{
	height: 5%;
	width: 100%;
	color: #333;
	font-size: 0.8em;
	margin: 5px;
	text-align: left;
}

@media (max-width:1000px){
	.title{
		font-size: 4em;
	}
	/* span{
		left: 0;	
	} */
	.item img{
		height: 75%;
	}
	.description{
		border-right: 1px solid #bbb;
	}
}

@media (max-width:800px){
	.title{
		font-size: 2em;
	}
	
	.item img{
		height: 85%;
	}

}



