

.container{
	position: relative;
	width: 100%;
	background-color: white;
	height: calc(100vh - 200px);
	margin: 0 auto;
	overflow: hidden;
	background-color: #e5e5e5;
	border-right: 1px solid #eee;
	border-left: 1px solid #eee;
	-webkit-animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) come forwards;
	        animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) come forwards;
}

@media (max-width: 960px) {
	.container {
		width: 100%;
	}
}


